<template>
	<view class="content">
		<view class="cu-list menu sm-border margin-top">
			<view class="cu-form-group">
				<view class="title">店铺名称</view>
				<input maxlength="30" class="text-right" placeholder="店铺名称" v-model="details.name" placeholder-class="placeholder"></input>
			</view>
			<view class="cu-form-group">
				<view class="title">店铺LOGO</view>
				<image v-if="details.img" @click="setLogo" class="cu-avatar radius bg-gray" :src="details.img"/>
				<view v-else class="cu-avatar radius bg-gray" @click="setLogo"></view>
			</view>
		</view>
		<button class="add-btn" @click="confirm">提交</button>
	</view>
</template>

<script>
	import Store from '../../api/store'
	export default {
		data() {
			return {
				details: {
					img: '',
					name: ''
				}
			}
		},
		onLoad(option){
			this.getList()
		},
		methods: {
			async getList(){
				
				const that = this
				Store.getDetails({},function(res){
					that.details = res
				})
			},
			setLogo(){
				const that = this
				uni.chooseImage({
				    count: 1,
				    sizeType: ['compressed'],
				    sourceType: ['album','camera'],
				    success: function (res) {
						uni.uploadFile({
						  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
						  filePath: res.tempFilePaths[0],
						  name: 'file',
						  header: {
							'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
						  },
						  formData: {
							type: 1,
							size: 1024 * 500
						  },
						  success(res) {
							that.details.img = res.data
						  },
						  fail(res) {
							that.$api.msg(res.message);
							return false
						  }
						})
				    }
				});
			},
			confirm(){
				let data = this.details
				const that = this
				if(!data.name){
					this.$api.msg('店铺名称有误');
					return;
				}
				Store.updateSubmit(data,function(res){
					that.$api.msg(`修改成功`);
					setTimeout(()=>{
						uni.navigateBack()
					}, 800)
				})
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
</style>
